/* like song button */
   .likeSongButton{
   background:#f17300;
   color:#f0f8ff;
   border:none;
   position:relative;
   height:60px;
   font-size:1.6em;
   padding:0 2em;
   cursor:pointer;
   transition:800ms ease all;
   outline:none;
   position: fixed;
    top: 50px;
    right: 10px;
 }
 .likeSongButton:hover{
   background:#f0f8ff;
   color:#f17300;
 }
 .likeSongButton:before,.likeSongButton:after{
   content:'';
   position:absolute;
   top:0;
   right:0;
   height:2px;
   width:0;
   background: #f17300;
   transition:400ms ease all;
 }
 .likeSongButton:after{
   right:inherit;
   top:inherit;
   left:0;
   bottom:0;
 }
 .likeSongButton:hover:before,.likeSongButton:hover:after{
   width:100%;
   transition:800ms ease all;
 }

 /* Welcome Text Animation */

 .textAnimation {
  text-align:center;
  font-weight:300;
  font-size:45px;
  line-height: normal;
  padding-top: 10px;
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 10;
  -webkit-transform: translate3d(0,0,0);
}


.welcome {
  display:inline-block;
  white-space:nowrap;
  overflow: hidden;
}

.welcome:first-of-type {    /* For increasing performance
                       ID/Class should've been used.
                       For a small demo
                       it's okaish for now */
  animation: showup 7s infinite;
}

.welcome:last-of-type {
  width:0px;
  animation: reveal 7s infinite;
}

.welcome:last-of-type span {
  margin-left:-355px;
  animation: slidein 7s infinite;
}

@keyframes showup {
    0% {opacity:0;}
    20% {opacity:1;}
    80% {opacity:1;}
    100% {opacity:0;}
}

@keyframes slidein {
    0% { margin-left:-800px; }
    20% { margin-left:-800px; }
    35% { margin-left:0px; }
    100% { margin-left:0px; }
}

@keyframes reveal {
    0% {opacity:0;width:0px;}
    20% {opacity:1;width:0px;}
    30% {width:350px;}
    80% {opacity:1;}
    100% {opacity:0;width:350px;}
}

